/**
 * Axios 확장
 * interceptor를 통한 request, response 제어부
 */
import axios from 'axios'
import * as Sentry from "@sentry/vue"

// Sentry 초기화
Sentry.init({
    dsn: 'https://897569faa08a284adcfc71b0c7347c0e@o4506936174379008.ingest.us.sentry.io/4506936174510080', // 여기에 Sentry 프로젝트의 DSN을 입력하세요
});

axios.defaults.baseURL = 'http://43.201.59.105:3000';

// refreshToken을 이용해 새로운 accessToken 발급받기
const refreshAccessToken = async () => {
    try {
        const user = JSON.parse(localStorage.getItem('user'));
        const response = await axios.post('/api/auth/token', {
            refreshToken: user.refreshToken,
        });

        // 새로운 accessToken을 받은 후 axios 헤더를 업데이트
        const newAccessToken = response.data.accessToken;
        user.accessToken = newAccessToken;

        // 사용자 객체를 다시 로컬 스토리지에 저장
        localStorage.setItem('user', JSON.stringify(user));

        // Axios 기본 Authorization 헤더를 업데이트
        axios.defaults.headers.common['Authorization'] = `Bearer ${newAccessToken}`;

        return newAccessToken;
    } catch (error) {
        console.error('Failed to refresh access token:', error);
        throw error;
    }
};

// Axios의 request 인터셉터 설정
axios.interceptors.request.use(function (config) {
    // 요청을 보내기 전에 수행할 로직을 작성합니다.
    console.log('요청 보내기 전:', config);
    
    // 1. 요청 헤더 설정: 인증 토큰이나 사용자 정보를 포함하는 요청 헤더를 설정합니다.
    const token = localStorage.getItem('accessToken');  // 예: 로컬스토리지에 저장된 토큰을 가져옵니다.
    if (token) {
        config.headers['Authorization'] = `Bearer ${token}`;
    }

    // 2. 요청 데이터 변경: 요청의 데이터를 변경하거나 가공할 수 있습니다.
    // config.data = { ...config.data, additionalParam: 'value' };


    // 3. 정상적으로 응답이 왔을때 데이터를 Sentry에 전달
    // Sentry.addBreadcrumb({
    //     category: 'axios-request',
    //     message: 'Sending request',
    //     data: {
    //         url: config.url,
    //         method: config.method,
    //         headers: config.headers,
    //         data: config.data
    //     }
    // });


    // 설정을 변경한 후에는 반드시 설정(config)을 반환해야 합니다.
    return config;
}, function (error) {
    // 요청 오류 시 수행할 로직을 작성합니다.
    console.error('요청 오류:', error);
    // 오류가 났을때 응답 데이터를 Sentry에 전달합니다.
    Sentry.addBreadcrumb({
        category: 'axios-response',
        message: 'Received response',
        data: {
            status: error.response.status,
            statusText: error.response.statusText,
            headers: error.response.headers,
            data: error.config.data
        }
    });
    // 에러 내용 수집
    Sentry.captureException(error);

    // Promise 오류를 반환합니다.
    return Promise.reject(error);
});

// Axios의 response 인터셉터 설정
axios.interceptors.response.use(
    function (response) {
        // 응답을 받은 후에 수행할 로직
        console.log('응답을 받은 후:', response);
        return response;  // 응답 데이터를 그대로 반환
    },
    async function (error) {
        console.error('응답 오류:', error);

        const originalRequest = error.config;
        const status = error.response ? error.response.status : null;

        // Access token이 만료된 경우 처리
        if (status === 401 && !originalRequest._retry) {
            originalRequest._retry = true;

            try {
                // 새 access token을 발급받아 원래 요청을 재시도
                const newAccessToken = await refreshAccessToken();

                // 기존 요청 헤더를 새로운 access token으로 업데이트
                originalRequest.headers['Authorization'] = `Bearer ${newAccessToken}`;

                // 원래의 요청을 재시도
                return axios(originalRequest);
            } catch (refreshError) {
                console.error('리프레시 토큰 오류:', refreshError);
                Sentry.captureException(refreshError);

                // 로그아웃 처리 등 필요한 작업 수행
                // 예: localStorage.clear(); window.location.href = '/login';

                return Promise.reject(refreshError);
            }
        }

        // 401 이외의 오류 처리 (404, 500 등)
        if (status) {
            switch (status) {
                case 404:
                    alert('요청한 페이지를 찾을 수 없습니다.');
                    break;
                case 500:
                    alert('서버에 오류가 발생했습니다. 잠시 후 다시 시도해 주세요.');
                    break;
                default:
                    alert('알 수 없는 오류가 발생했습니다.');
            }
        }

        // Sentry에 오류 기록
        // Sentry.addBreadcrumb({
        //     category: 'axios-response',
        //     message: 'Received error response',
        //     data: {
        //         status: error.response?.status,
        //         statusText: error.response?.statusText,
        //         headers: error.response?.headers,
        //         data: error.config?.data,
        //     }
        // });

        // Sentry에 오류 기록
        Sentry.captureException(error);

        // Promise 오류를 반환
        return Promise.reject(error);
    }
);

export default axios;


