<template>
    <div v-if="showPopup" class="intro-popup">
      <div class="popup-content">
        <h1>FoodFinderAI</h1>
        <p>Find the best food around you in seconds!</p>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        showPopup: true, // 팝업이 처음에 보이게 설정
      };
    },
    mounted() {
      // 5초 후에 팝업을 사라지게 설정
      setTimeout(() => {
        this.showPopup = false;
      }, 5000);
    },
  };
  </script>
  
  <style scoped>
  .intro-popup {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: orange; /* 주황색 배경 */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 9999;
    color: white; /* 흰색 글씨 */
    font-family: 'Poppins', sans-serif; /* Poppins 폰트 사용 */
  }
  
  .popup-content {
    text-align: center;
    padding: 20px;
    border-radius: 10px;
    background: rgba(0, 0, 0, 0.3); /* 약간의 그림자 효과 */
  }
  
  h1 {
    margin-bottom: 10px;
    font-weight: 600; /* 두꺼운 폰트 */
    font-size: 2.5em; /* 적절한 폰트 사이즈 조정 */
  }
  
  p {
    font-weight: 400; /* 일반 폰트 */
    font-size: 1.5em; /* 적절한 폰트 사이즈 조정 */
  }
  </style>


