<template>
  <div class="container">
    <div
      class="circle"
      :class="{ active: isActive }"
      @click="onCircleClick"
    >
      <!-- 작은 원들 렌더링 -->
      <SmallCircle
        v-for="(circle, index) in circles"
        :key="index"
        :circle="circle"
        @addCircle="addCircle(index)"
      />
    </div>
  </div>
</template>

<script>
import SmallCircle from './Circle.vue'; //Circle 컴포넌트 임포트

export default {
  components: {
    SmallCircle
  },
  data() {
    return {
      isActive: false,
      circles: [], // 작은 원들 위치 정보
    };
  },
  methods: {
    onCircleClick() {
      this.toggleCircles();
      this.addInitialCircles(); // 초기 작은 원들 추가
    },
    toggleCircles() {
      this.isActive = !this.isActive;
    },
    addInitialCircles() {
      if (!this.circles.length) {
        // 초기 네 개의 작은 원을 중앙 원 주위에 배치
        this.circles = [
          { level: 1, position: this.calculateNewPosition(0) },
          { level: 1, position: this.calculateNewPosition(90) },
          { level: 1, position: this.calculateNewPosition(180) },
          { level: 1, position: this.calculateNewPosition(270) },
        ];
      }
    },
    addCircle(index) {
      // 클릭된 원 기준으로 새로운 작은 원 추가
      const parentCircle = this.circles[index];
      const newLevel = parentCircle.level + 1;
      const newCircle = {
        level: newLevel,
        position: this.calculateNewPosition(Math.random() * 360, parentCircle.position),
      };
      this.circles.push(newCircle);
    },
    calculateNewPosition(angle, parentPosition = { top: '0px', left: '0px' }) {
      const radius = 100; // 중앙 원 또는 부모 원에서 떨어진 거리
      const radian = (angle * Math.PI) / 180;

      const topValue = parseFloat(parentPosition.top) + Math.sin(radian) * radius;
      const leftValue = parseFloat(parentPosition.left) + Math.cos(radian) * radius;

      const top = `${topValue}px`;
      const left = `${leftValue}px`;

      return { top, left, transform: 'translate(-50%, -50%)', position: 'absolute' };
    },
  },
};
</script>

<style scoped>
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background: radial-gradient(circle, #000428, #004e92);
  overflow: hidden;
}

.circle {
  width: 120px;
  height: 120px;
  background: linear-gradient(145deg, #0066ff, #00ccff);
  border-radius: 50%;
  position: relative;
  animation: moveUpDown 2s infinite ease-in-out; /* 중앙 원 애니메이션 */
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease-in-out;
  z-index: 2;
}

.circle.active {
  transform: scale(1.1);
}

@keyframes moveUpDown {
  0%, 100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-15px);
  }
}
</style>


