<template>
    <div
      class="small-circle"
      :style="circle.position"
      @click.stop="emitAddCircle"
    ></div>
  </template>
  
  <script>
  export default {
    props: ['circle'],
    methods: {
      emitAddCircle() {
        this.$emit('addCircle');
      },
    },
  };
  </script>
  
  <style scoped>
  .small-circle {
    width: 60px;
    height: 60px;
    background: linear-gradient(145deg, #ff0099, #493240);
    border-radius: 50%;
    position: absolute;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3);
    cursor: pointer;
    transition: all 0.3s ease-in-out;
    z-index: 1;
  }
  </style>


