<template>
    <div>
        <section class="notice" style="width: 200vw;">
            <div class="container">
                <h3 class="text-center mb-4" style="color: white;">My AI</h3>

                <!-- Categories Section -->
                <div class="categories">
                    <div v-for="(category, index) in categories" :key="index" class="category"
                        @mouseover="hoverCategory(index)" @mouseleave="leaveCategory(index)"
                        @click="selectCategory(index)" :class="{ selected: selectedCategory === index }">
                        {{ category }}
                    </div>
                </div>

                <!-- Add space between Categories and Input Section -->
                <div class="categories-spacer"></div>

                <!-- Input and Search Section -->
                <div class="row justify-content-center mb-4">
                    <div class="col-md-3"> <!-- 너비를 반으로 줄임 (기존 col-md-6에서 col-md-3으로 변경) -->
                        <div class="input-group input-category">
                            <input type="text" class="form-control" placeholder="키워드를 입력해주세요">
                            <button class="btn btn-primary" type="button">Search</button>
                        </div>
                    </div>
                </div>

                <!-- Add space between Categories and Chart Area -->
                <!-- <div class="spacer"></div> -->

                <!-- Chart Section -->
                <!-- <div class="row mb-4">
                    <div class="col-md-6 mb-4">
                        <div id="healthChartBar" style="height: 400px;"></div>
                    </div>
                    <div class="col-md-6 mb-4">
                        <div id="healthChartLine" style="height: 400px;"></div>
                    </div>
                </div> -->

                <!-- Balloon Section for Selected Categories -->
                <div class="balloon-section">
                    <div v-for="(balloon, index) in selectedBalloons" :key="index" class="balloon"
                        style="max-width: 40%;">
                        <img :src="balloon.image" alt="Health Image" class="balloon-image">
                        <div class="balloon-text">
                            {{ balloon.text }}
                        </div>
                    </div>
                </div>
            </div>
        </section>
    </div>
</template>

<script>
import * as echarts from 'echarts';
import CircleAnimation from '../../components/animation/CircleAnimation.vue';

export default {
    components: {
        CircleAnimation
    },
    data() {
        return {
            categories: ['운동', '영양', '수면', '정신건강', '라이프스타일'],
            selectedCategory: null,
            selectedBalloons: [], // 말풍선으로 추가될 데이터 리스트
            balloonData: {
                운동: {
                    text: "규칙적인 근력 운동과 유산소 운동이 필수적입니다. 적절한 운동은 체지방 감소와 근육량 유지를 도와줍니다.",
                    image: "https://via.placeholder.com/150/007bff/FFFFFF?text=Fitness"
                },
                영양: {
                    text: "건강한 체중을 유지하려면 단백질, 탄수화물, 지방의 균형 잡힌 섭취가 중요합니다. 신선한 과일과 채소도 충분히 포함시켜야 합니다.",
                    image: "https://via.placeholder.com/150/28a745/FFFFFF?text=Nutrition"
                },
                수면: {
                    text: "적절한 수면은 신체 회복을 위한 깊은 잠을 자는 것도 중요합니다.",
                    image: "https://via.placeholder.com/150/ff6600/FFFFFF?text=Sleep"
                },
                정신건강: {
                    text: "정신적 건강은 신체 건강만큼 중요합니다. 스트레스 관리와 취미 활동을 통해 정서적 안정을 찾으세요.",
                    image: "https://via.placeholder.com/150/6f42c1/FFFFFF?text=Mental+Health"
                },
                라이프스타일: {
                    text: "건강한 라이프스타일을 위해서는 균형 잡힌 식사와 규칙적인 운동이 필수적입니다.",
                    image: "https://via.placeholder.com/150/20c997/FFFFFF?text=Lifestyle"
                }
            },
        };
    },
    methods: {
        hoverCategory(index) {
            // Additional hover logic can go here
        },
        leaveCategory(index) {
            // Additional leave logic can go here
        },
        selectCategory(index) {
            this.selectedCategory = index;
            this.addBalloon(index);
        },
        addBalloon(index) {
            const category = this.categories[index].replaceAll(' ', '');
            const balloon = this.balloonData[category];

            // 이미 선택된 데이터인지 확인
            const isAlreadySelected = this.selectedBalloons.some(existingBalloon => existingBalloon.text === balloon.text);

            // 선택된 데이터가 없다면 배열에 추가
            if (!isAlreadySelected) {
                this.selectedBalloons.push(balloon);
            }
        },
        createBarChart(option) {
            const chartDom = document.getElementById('healthChartBar');
            if (chartDom) {
                const myChart = echarts.init(chartDom);
                myChart.setOption(option);
            }
        },
        createLineChart() {
            const chartDom = document.getElementById('healthChartLine');
            if (chartDom) {
                const myChart = echarts.init(chartDom);
                const option = {
                    title: {
                        text: 'Health Metrics - Line Chart',
                        left: 'center'
                    },
                    tooltip: {},
                    xAxis: {
                        type: 'category',
                        data: ['Metric 1', 'Metric 2', 'Metric 3']
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        data: [10, 20, 30],
                        type: 'line',
                        color: '#2ca02c'
                    }]
                };
                myChart.setOption(option);
            }
        }
    },

    mounted() {
        // this.createBarChart(option);
        // this.createLineChart();
    }
};
</script>

<style scoped>
.container {
    max-width: 100%;
    min-height: 860px;
    padding-top: 55px;
    padding-bottom: 20px;
    background: radial-gradient(circle, #000428, #004e92);
}

.categories {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-top: 20px;
}

.category {
    padding: 10px 20px;
    border-radius: 20px;
    background-color: #f0f0f0;
    cursor: pointer;
    transition: all 0.3s ease;
}

.category:hover {
    background-color: #007bff;
    color: white;
    transform: scale(1.1);
}

.category.selected {
    background-color: #0056b3;
    color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

/* Spacer between categories and input section */
.categories-spacer {
    height: 20px; /* 카테고리 영역과 입력 섹션 사이의 공백 추가 */
}

/* Chart area adjustments */
#healthChartBar,
#healthChartLine {
    height: 400px;
}

/* Spacer between categories and chart area */
.spacer {
    height: 30px;
}

/* Balloon section */
.balloon-section {
    margin-top: 40px;
}

.balloon {
    display: flex;
    align-items: center;
    background-color: #f1f1f1;
    padding: 20px;
    margin: 10px auto; /* 여백을 위해 상단/하단은 10px, 좌우는 auto */
    border-radius: 20px; /* 모서리를 동그랗게 */
    animation: fadeIn 0.5s ease-in-out;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* 약간의 그림자 추가 */
    max-width: 80%; /* 전체 화면에서 최대 너비를 80%로 제한 */
}

.balloon-image {
    width: 80px;
    height: 80px;
    margin-right: 20px;
    border-radius: 50%;
    object-fit: cover;
}

.balloon-text {
    flex: 1;
    font-size: 16px;
}

/* Animation */
@keyframes fadeIn {
    from {
        opacity: 0;
        transform: translateY(20px);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}
</style>


