<template>
  <div id="app">
    <!-- <Intro v-if="this.$commonUtil.isMobile()" /> -->
    <DefaultLayout>
      <router-view />
    </DefaultLayout>
  </div>
</template>

<script>
import DefaultLayout from '@/layouts/DefaultLayout.vue'
import Intro from '@/layouts/MobileIntro.vue';


export default {
  name: 'App',
  components: {
    DefaultLayout,
    Intro
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

a {
  cursor: pointer; /* Apply hand cursor to all <a> elements */
}
</style>



