<template>
    <div id="app">
      <button @click="captureImage">Capture/Select Image</button>
      <img v-if="capturedImage" :src="capturedImage" alt="Captured Image" />
  
      <p v-if="!flutterAvailable">FlutterChannel not available</p>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        capturedImage: null,
        flutterAvailable: false,
      };
    },
    methods: {
      captureImage() {
        // Check if FlutterChannel is available, then trigger the capture or select
        if (window.FlutterChannel) {
          window.FlutterChannel.postMessage('captureImage');
          this.flutterAvailable = true;
        } else {
          console.log("FlutterChannel not available");
          this.flutterAvailable = false;
        }
      },
      handleCapturedImage(base64Image) {
        // Display the captured or selected image
        this.capturedImage = base64Image;
      },
    },
    mounted() {
      // Make the handleCapturedImage function available globally for Flutter to call
      window.handleCapturedImage = this.handleCapturedImage;
    },
  };
  </script>
  
  <style>
  #app {
    text-align: center;
    margin-top: 50px;
  }
  button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
  }
  img {
    margin-top: 20px;
    max-width: 100%;
    height: auto;
  }
  </style>


