<template>
  <header>
    <nav class="navbar navbar-expand-lg navbar-dark">
      <div class="container-fluid">
        <!-- 모바일 백버튼 -->
        <BaseButton v-show="this.$commonUtil.isMobile()" className="btn btn-outline-light me-2 back-button" type="button" :debounceEnabled="false" :clickHandler="handleBackClick">
          <i class="bi bi-arrow-left"></i> <!-- Bootstrap Icons for the back arrow -->
        </BaseButton>
        <!-- 로고 또는 사이트 이름 -->
        <a class="navbar-brand">
          <router-link to="/" class="nav-link">Amigo</router-link>
        </a>
        <!-- 햄버거 아이콘 -->
        <BaseButton className="navbar-toggler" type="button" :clickHandler="toggleNavbar">
          <span class="navbar-toggler-icon"></span>
        </BaseButton>
        <!-- 메뉴 목록 -->
        <div class="collapse navbar-collapse" id="navbarNav">
          <ul class="navbar-nav me-auto mb-2 mb-lg-0">
            <li class="nav-item dropdown">
              <a class="nav-link" role="button" id="moduleDropdown" data-bs-toggle="dropdown"
                aria-expanded="false">Module</a>
              <!-- 서브메뉴 -->
              <ul class="dropdown-menu" aria-labelledby="moduleDropdown">
                <li><a class="dropdown-item" @click="$goToPage('MyAI')">MyAI</a></li>
                <li><a class="dropdown-item" @click="$goToPage('Test')">Test</a></li>
                <!-- <li><router-link to="/MyAI" class="dropdown-item">MyAI</router-link></li>
                <li><router-link to="/module2" class="dropdown-item">Role</router-link></li> -->
                <!-- 다른 모듈 추가 -->
              </ul>
            </li>
            <li class="nav-item">
              <a class="nav-link" @click="$goToPage('Board')">Q&A</a>
            </li>
            <li class="nav-item">
              <a class="nav-link" @click="$goToPage('')">Career</a>
            </li>
            <!-- 기타 메뉴 항목 추가 -->
          </ul>
          <!-- 사용자 관련 메뉴 -->
          <div v-if="user" class="d-flex align-items-center">
            <span class="text-light me-3">{{ user.name + '님' }}</span>
            <!-- 마이페이지 드롭다운 -->
            <div class="dropdown">
              <BaseButton className="btn btn-outline-secondary dropdown-toggle" type="button" id="dropdownMenuButton"
                data-bs-toggle="dropdown" aria-expanded="false">마이페이지</BaseButton>
              <!-- 드롭다운 메뉴 -->
              <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton">
                <li><a class="dropdown-item" @click="$goToPage('EditProfile')">회원정보</a></li>
                <li><a class="dropdown-item" @click="deleteUser()">탈퇴</a></li>
                <!-- 다른 메뉴 아이템 추가 -->
              </ul>
            </div>
            <!-- 로그아웃 버튼 -->
            <BaseButton className="btn btn-outline-danger ms-3" type="button" :clickHandler="() => logout()">Logout
            </BaseButton>
          </div>
          <!-- 로그인 및 회원가입 버튼 -->
          <div v-else>
            <BaseButton className="btn btn-outline-light" type="button" :clickHandler="() => $goToPage('Login')">Login
            </BaseButton>
            <BaseButton className="btn btn-outline-light" type="button" :clickHandler="() => $goToPage('JoinMember')">Join
            </BaseButton>
          </div>
        </div>
      </div>
    </nav>
  </header>
</template>

<script>
import BaseButton from '@/components/base/BaseButton';

export default {
  name: 'Header',
  components: {
    BaseButton // Registering the Button.vue component
  },
  data() {
    return {};
  },
  computed: {
    user() {
      return this.$store.getters.user; // Computed property for user state
    },
  },
  methods: {
    toggleNavbar() {
      const navbar = document.getElementById('navbarNav');
      if (navbar) {
        const expanded = navbar.getAttribute('aria-expanded') === 'true';
        navbar.setAttribute('aria-expanded', !expanded); // Toggle aria-expanded
        navbar.classList.toggle('show'); // Toggle navigation bar
      }
    },
    logout() {
      // Send logout request to server
      this.$axios.post("api/auth/logout")
        .then((res) => {
          if (res.data.success) {
            // On successful logout, clear local state and navigate to login page
            this.$store.commit('clearUser');
            this.$goToPage('Login');
          } else {
            alert("Logout failed.");
          }
        })
        .catch((err) => {
          console.error(err);
        });
    },
    deleteUser() {
      const userId = this.$store.getters.user.id;
      const confirmDelete = window.confirm('Are you sure you want to delete your account?');

      // Send delete request if user confirms
      if (confirmDelete) {
        this.$axios.delete(`/api/user/${userId}`)
          .then(response => {
            console.log('User deleted successfully:', response.data);
            alert("User successfully deleted.");
            this.$store.commit("setUser", null);
            this.$goToPage('Login');
          })
          .catch(error => {
            console.error('User deletion failed:', error.response.data);
          });
      }
    },
    goBack() {
      this.$router.go(-1);
    },
    handleBackClick(event) {
      const button = event.target.closest('.back-button');
      if (button) {
        button.classList.add('blinking');
        setTimeout(() => {
          button.classList.remove('blinking');
          this.goBack();
        }, 500);
      }
    }
  },
}
</script>

<style scoped>
/* 헤더에만 적용되는 스타일 */
/* 헤더 스타일 */
header {
  position: fixed;
  /* 헤더를 화면 위쪽에 고정 */
  top: 0;
  /* 화면 상단에 위치 */
  width: 100%;
  /* 가로폭 전체 너비를 차지 */
  background-color: #222;
  /* 약간의 검은색 배경색 */
  color: white;
  /* 글자색 지정 */
  padding: 10px;
  /* 여백 추가 */
  z-index: 2;
  /* 헤더를 컨텐츠 위로 올림 */
}

.dropdown:hover .dropdown-menu {
  display: block;
}

.dropdown-menu {
  display: none;
}

.dropdown-menu:hover {
  display: block;
}

.back-button {
  position: relative;
}

.blinking {
  animation: blink 0.5s;
}

@keyframes blink {
  0% { opacity: 1; }
  50% { opacity: 0; }
  100% { opacity: 1; }
}

.back-button .back-arrow {
  display: inline-block;
  width: 0;
  height: 0;
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  border-right: 8px solid white; /* Color of the arrow */
  margin-right: 5px; /* Spacing between arrow and text */
}
</style>


